.d-desc {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.d-desc .title {
  margin-bottom: 15px;
  width: 200px;
  text-align: right;
  word-break: break-word;
  font-weight: bold;
}
.d-desc .item {
  display: flex;
  margin-bottom: 10px;
  font-size: 12px;
}
.d-desc .item .item-title {
  position: relative;
  flex: 0 1 auto;
  width: 200px;
  word-break: break-word;
  text-align: right;
  line-height: 1.8;
}
.d-desc .item .item-title:after {
  content: ':';
  position: absolute;
  top: -0.5px;
  margin: 0 8px 0 4px;
}
.d-desc .item .item-content {
  flex: 1 1;
  width: auto;
  margin-left: 16px;
  word-break: break-word;
  line-height: 1.8;
}
